body {padding: 50px}

.chart {
    margin-bottom: 100px;
}

canvas._chart {
    user-select: none;
    width: 100%;
    height: 400px;
    // border: 1px solid gray;
    position: relative;
}

canvas._chart-map {
    user-select: none;
    margin-top: 20px;
    position: relative;
    width: 100%;
    height: 50px;
}

ul._chart-legend {
    user-select: none;
    list-style: none;
    padding-left: 0;

    > li {
        display: inline-block;
        margin-right: 1em;
    }
}

